import {
  Button,
  Col,
  DatePicker,
  Form,
  Input,
  Modal,
  Row,
  Select,
  Space,
  TimePicker,
} from "antd";
import React, { useEffect, useRef, useState } from "react";
import TextArea from "antd/lib/input/TextArea";
import { UploadOutlined } from "@ant-design/icons";

const { RangePicker } = DatePicker;
const { RangeTimePicker } = TimePicker;
const destinations = [
  {
    value: "H Ni",
    label: "H Ni",
  },
  {
    value: " Nng",
    label: " Nng",
  },
  {
    value: "in Bin",
    label: "in Bin",
  },
  {
    value: "Cn Th",
    label: "Cn Th",
  },
  {
    value: "Hi Phng",
    label: "Hi Phng",
  },
  {
    value: "TP. H Ch Minh",
    label: "TP. H Ch Minh",
  },
];

const CardAddFlight = (props) => {
  const [isModalVisible, setIsModalVisible] = useState(false);

  const showModal = () => {
    setIsModalVisible(true);
  };

  const handleOk = () => {
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  return (
    <>
      <Modal
        title="Thm chuyn bay"
        visible={props.isModalVisible}
        onCancel={props.handleCancel}
        centered
        onOk={handleOk}
      >
        <Row justify="start" className="Style">
          <Col xs={8} md={6}>
            <span>Plane Name:</span>
          </Col>
          <Col xs={16} md={18}>
            <Select style={{ width: "355px" }}/>
          </Col>
        </Row>
        <Row justify="start" className="Style">
          <Col xs={8} md={6}>
            <span>Flight Name:</span>
          </Col>
          <Col xs={16} md={18}>
            <Input style={{ width: "100%" }} placeholder="Flight Name..." />
          </Col>
        </Row>
        <Row style={{ marginTop: "10px" }}>
          <Col xs={8} md={6}>
            <label>Chn im i</label>
          </Col>
          <Col xs={8} md={6}>
            <Select
              placeholder="Chn im i..."
              options={destinations}
              style={{ width: "355px" }}
            />
          </Col>
        </Row>
        <Row style={{ marginTop: "10px" }}>
          <Col xs={8} md={6}>
            <label>Chn im n</label>
          </Col>
          <Col xs={8} md={6}>
            <Select
              placeholder="Chn im n..."
              options={destinations}
              style={{ width: "355px" }}
            />
          </Col>
        </Row>
        <Row>
          <Col xs={8} md={6}>
            <label>Gi</label>
          </Col>
        </Row>
        <Row >
          <Col xs={8} md={6}>
            <label>Chn ngy</label>
          </Col>
          <Col xs={8} md={6}>
            <Space direction="vertical" size={14}>
              <RangePicker
                style={{ width: "355px", borderRadius: "1px" }}
              />
            </Space>
          </Col>
        </Row>
        <Row style={{ marginTop: "10px" }} >
          <Col xs={8} md={6}>
            <label>Thi gian i</label>
          </Col>
          <Col xs={8} md={6}>
              <TimePicker placeholder="Thi gian i..." style={{ width: "355px", borderRadius: "1px" }}/>
          </Col>
        </Row>
        <Row style={{ marginTop: "10px" }}>
          <Col xs={8} md={6}>
            <label>Thi gian n</label>
          </Col>
          <Col xs={8} md={6}>
              <TimePicker placeholder="Thi gian n..." style={{ width: "355px", borderRadius: "1px" }}/>
          </Col>
        </Row>
      </Modal>
    </>
  );
};

export default CardAddFlight;
